.custom-carousel{
    position: relative;
    min-height:400px;
    max-height:600px;
    width:100%;
    position: relative;
    margin:0 auto;
  
    display:flex;
    justify-content: center;
    justify-items: center;
    flex-direction: column;
}
.carousel-img{
    position: relative;
    display:flex;
    justify-content: center;
    align-items: center;
  
}
.carousel-img img{
    width:700px;
    height:300px;
    position: relative;
    margin:0 auto;

}
.carousel-controls{
    display:flex;
    position: relative;
    margin:0 auto;
    min-width:400px;
    justify-content: space-between;

}
.carousel-controls div{
    width:30px;
    height:30px;
    display:flex;
    justify-content: center;
    align-items:center;
    border:1px solid;
    border-radius: 50%;
    text-align: center;
    cursor: pointer;
    transition:all 0.5s;
}
.carousel-controls div:hover{
    background-color: #EEFF00;
}
.carousel-controls div >i{
    text-align: center;
}
.carousel-text{
    width:300px;
    display:flex;
    flex-direction: column;
    justify-items: center;
    text-align: center;
    position: relative;
    margin:0 auto;
    font-family:Oswald ,sans-serif;
    top:-50px;
    min-height:200px;
    
}
.carousel-license{
    font-size:6pt;
    font-size:100;
    text-transform: uppercase;
}
.carousel-title{
    font-size:18pt;
    font-weight:600;
}
.carousel-description{
    font-size:8pt;
    letter-spacing:1px;
}
.carousel-text .properties{
    display:flex;
    font-size:6pt;
    text-align: center;
    justify-content: center;
    position: relative;
    top:10px;
}
.carousel-text .properties > div:not(:last-child){
    margin-right:23px;
    border-right:0.5px solid;
    padding-right:23px;
}
.carousel-number{
    font-size:60pt;
    bottom:20px;
    width:200px;
    font-weight: bold;
    display:flex;
    justify-content: flex-end;
}
.carousel-footer{
    width:100%;
    display:flex;
    justify-content: space-between;
    align-items: center;

    position: absolute;
    bottom:50px;
    padding:0 10px;
}
.carousel-social{
    width:200px;
    display:flex;
    align-items: center;
    justify-content: flex-start;
}
.carousel-social div{
    margin-right:10px;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction:row;
    width:30px;
    height:30px;
    border-radius:50%;
    border:0.5px solid;
    text-align: center;
    padding-right:2px;
    transition:all ease-in 0.5s
}
.carousel-social div:hover{
    background-color:#EEFF00;
    border-width:0px;
    cursor:pointer
}
.carousel-sell{
    text-align: center;
    font-size:8pt;
    font-family:Oswald ,sans-serif;
    width:200px;
}
.carousel-buynow{
    position: absolute;
    display:flex;
    justify-content: center;
    align-items: center;
    font-family:Oswald ,sans-serif;
    background-color:#292F33;
    color:#FFFFFF;
    border:5px solid #EAFF00;
    border-radius:50%;
    height:60px;
    width:60px;
    transition:all ease-in 0.5s;
}
.carousel-buynow:hover{
    border-color:#AFFF00;
    color:white;
}
