/*
------------
Global Styles
------------
*/
.chairs{   
    position: relative;
    display:flex;
    flex-direction: column;
    justify-items: center;
    justify-content: center;
    font-family: 'Oswald', sans-serif;
    width:100%;
    height:500px;
    margin:0 auto;
    margin-top:250px;
}
.chairs-container{
    display:grid;
    grid-template-rows:4fr 4fr 4fr;
    justify-items: center;
    width:100%;
    height:100%;
    position: relative;
    z-index:2;
}
.chairs-plus-minus-container{
    position: absolute;
    display:flex;
    justify-content: space-between;
    width: 90%;
    height:100%;
    z-index:1;
    margin:0 auto;
    align-self: center;
   
}   
.chairs-relative-container{
    display:grid;
    justify-content: space-between;
    align-content: center;
    width:90%;
    background-color: red;
    position: absolute;
    left:5%;
    z-index: 2;
}
.chairs-filling-container{
    position: absolute;
    display:grid;
    grid-template-columns:4fr 300px 4fr;
    width:min(90%,100%);
    height:100px;
    align-self:center;
    align-items: center;
    left:5%;
    grid-gap:20px;

}
